* {
    margin: 7px;
  
    padding: 5px;
  
    box-sizing: border-box;
  
  }
  
   
  
  .App,
  
  .App1 {
  
    text-align: center;
  
    background-color: #000000;
  
    background-size: 100;
  
    text-decoration-thickness: initial;
  
    text-decoration: nonderline;
  
    color: #eee;
  
    font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  
  }
  
   
  
  .labell {
  
    color: black;
  
    text-decoration: bold;
  
  }
  
  h1,
  
  h2 {
  
    text-decoration: underline;
  
  }
  
   
  
  .form,
  
  .form1 {
  
    border: 1px solid;
  
    display: flex;
  
    flex-direction: column;
  
    align-items: center;
  
    justify-content: left;
  
    padding: 10px;
  
    background-color: lightgrey;
  
  }
  
   
  
  .form input,
  
  .form1 input {
  
    width: 400px;
  
    background-color: rgb(255, 255, 255);
  
    height: 40px;
  
    margin: 10px;
  
    padding: 10px;
  
    font-size: 20px;
  
    font-family: "Times New Roman", Times, serif;
  
  }
  
   
  
  .list {
  
    width: 900px;
  
    margin: 90px auto;
  
  }
  
  .list h2 {
  
    font-size: 40px;
  
    margin-bottom: 20px;
  
  }
  
  .list table {
  
    width: 100%;
  
    text-align: center;
  
  }
  
  .list table thead tr {
  
    background: #555;
  
    color: #eee;
  
  }
  
  .list table thead tr th,
  
  .list table tbody tr td {
  
    padding: 5px;
  
  }
  
  .list table tbody tr:nth-child(odd) {
  
    background: #eee;
  
  }
  
   
  
  div#output-short-link.form-control {
  
    color: #000000;
  
  }
  
   
  
  .btn {
  
    float: right;
  
  }
  
